.worldMap {
  background-image: url("../../assets/continents/map.png");
  height: calc(65vw / 2.02);
  width: 65vw;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-position: center;
  background-size: cover;
  z-index: 10;
}
