@import "../../colors.scss";

.exitModal {
  position: absolute;
  top: 5%;
  right: 5%;
  cursor: pointer;
  img {
    width: 2vw;
  }
}

.modal {
  width: 55vw;
  height: 60vh;
  position: absolute;
  background-color: $lightBlue;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 2vw 4vw;
  border-radius: 20px;
  box-shadow: -0.3vw 0.2vw 0px 0.1vw $darkBLue;
  z-index: 20;
  animation: onLoad 0.5s ease;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@keyframes onLoad {
  0% {
    height: 0vw;
    width: 0vw;
  }
  100% {
    height: 60vh;
    width: 55vw;
  }
}

.paragrapheScroll {
  scroll-behavior: smooth;
  overflow-y: scroll;
  max-height: 40vh;
}
.titleModalCenter {
  text-align: center;
}
.paragrapheScroll::-webkit-scrollbar {
  width: 10px;
  height: 8px;
  background-color: #2f4c54; /* or add it to the track */
  color: #2f4c54;
  border-radius: 10px;
}
.paragrapheScroll::-webkit-scrollbar-thumb {
  background: #162830;
  height: 1px;
  border-radius: 10px;
}
.modalFactoryContainer {
  .modalFactoryHeader {
    display: flex;
    justify-content: space-around;
  }
  h3,
  h4 {
    font-weight: 500;
    font-size: 1.5vw;
    margin: 3vh 0;
  }
}

.modalButtonDark {
  background-color: $green;
  box-shadow: -0.3vw 0.2vw 0px 0.1vw $darkBLue;
}
.modalButtonLight {
  background-color: $lightGreen;
  box-shadow: -0.3vw 0.2vw 0px 0.1vw $green;
}

.pictogramContainerModalBuy {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 80%;

  .activePower {
    border: 3px solid white !important;
    border-radius: 15px;
  }
  .pictogramModalBuy {
    padding: 2vh;
    border: 3px solid $lightBlue;
    cursor: pointer;
    z-index: 800;
    * {
      pointer-events: none;
      z-index: 100;
    }

    img {
      width: 5vw;
    }
    .descBuyPictogram {
      div {
        text-align: center;
        font-size: 1.1vw;
      }
    }
  }
}

.btnCenterContainer {
  text-align: center;
}
.continentChooseModalBuy {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 2vh;
  width: 100%;
  h3 {
    margin-right: 25%;
  }
  .modalButtonLight {
    text-transform: capitalize;
  }
}

.endGameModal {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  text-align: center;
  backdrop-filter: blur(3px);
  z-index: 500;

  .modalContentEndGame {
    height: 60%;
    width: 60%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 2vw 4vw;
    border-radius: 20px;
    box-shadow: -0.3vw 0.2vw 0px 0.1vw $darkBLue;
    background-color: $lightBlue;
  }
  .contentEndGame {
    height: 80%;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  h1 {
    font-family: goodTime !important;
    margin: 1.5em 0;
  }
  h2 {
    font-weight: 600;
  }
}

.chooseAContinent {
  display: flex;
  align-items: center;
  margin-bottom: 2vh;
  justify-content: flex-start;
  h4 {
    width: 40%;
  }
  .continentContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: safe center;
    flex-direction: column;
    margin: auto;
    background-color: $lightGreen;
    box-shadow: -0.3vw 0.2vw 0px 0.2vw $green;
    border-radius: 2px;
    align-items: center;
    .continentToSelect {
      display: none;
    }
    button {
      background-color: $lightGreen;
    }
  }
}

.modalFactoryContainer {
  height: auto !important;
}

.selectedRole {
  background-color: $green;
  box-shadow: none;
  padding: 0.1em 0.4em;
}

.disableConfirmBtn {
  pointer-events: none;
  background-color: $darkBLue;
  box-shadow: none;
}
